import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { IonSlides, ModalController } from '@ionic/angular';
import { Funciones } from '../compartido/funciones';
import { ApiMongoService } from '../services/apiMongo.service';
import { IonicComponentsService } from '../services/ionic-components.service';
import { environment as env } from 'src/environments/environment';
import { StorageService } from '../services/storage.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {


  @ViewChild('slidesPadre', { static: false }) private slidesPadre: IonSlides;
  public loginForm: FormGroup;
  public viewPass = false;
  public tipo = "password";
  public submitAttempt: boolean = false;
  constructor(
    
    private _apiMongo: ApiMongoService,
    private _fun: Funciones,
    public _storage: StorageService,
    public router: Router,
    public _comp: IonicComponentsService,
    public formBuilder: FormBuilder,
    private _modal: ModalController) { 
      this.loginForm = formBuilder.group({
        username: ['', Validators.compose([Validators.pattern("[^@]+@[^@]+\.[^@]+$"), Validators.required])],
        pass: ['', Validators.required]
      });
    }


  slideOptsOnboarding = {
    allowSlideNext: false,
    allowSlidePrev: false,
    slidesPerView: 1,
    initialSlide: 0,
    speed: 400
  };

  ngOnInit() {
  }

  mostrar() {
    if (this.viewPass) {
      this.tipo = "password";
      this.viewPass = false;
    } else {
      this.tipo = "text";
      this.viewPass = true;
    }
  }


  idioma(contry){
    //es
    //us
    this.nextSlidePadre();

  }



  async ionViewDidEnter() {
/* this.codePartida(); */


    this.backSlidePadre();
    this.backSlidePadre();
    this.backSlidePadre();
    this.backSlidePadre();
  }


  async login() {
    this.submitAttempt = true;
    let loading:any  = await this._comp.presentLoading();
    
    if (!this.loginForm.valid){
      loading.dismiss();
      return; 
    } 

    try {
      const res: any = await this._apiMongo.filter(env.COLLECTION.party, env.TABLE_SIS.customer, { 'data.email': this.loginForm.value.username, 'data.password': this.loginForm.value.pass });
      if (this._fun.isEmpty(res.result)) {
        loading.dismiss();
        await this._fun.alertError('Usuario no existe');
        return;
      }

      let user = res.result[0][env.TABLE_SIS.customer][0];
      this._storage.setLocalStorage('USER',user)
      loading.dismiss();
      this.router.navigate(['/home']);
    } catch (error) {
      await this._fun.alertError(error);
      loading.dismiss();
    }

  }

async invitado(){
  let loading:any  = await this._comp.presentLoading();
  this._storage.setLocalStorage('USER','INVITADO');
  loading.dismiss();
  this.router.navigate(['/home']);
}

  nextSlidePadre() {
    this.slidesPadre.lockSwipeToNext(false);
    this.slidesPadre.slideNext();
    this.slidesPadre.lockSwipeToNext(true);
  }

  backSlidePadre() {
    this.slidesPadre.lockSwipeToPrev(false);
    this.slidesPadre.slidePrev();
    this.slidesPadre.lockSwipeToPrev(true);
  }

  goImport() {
    this.router.navigate(['/import-wallet']);
  }

}